import { Component, h, Prop, Event, EventEmitter, Watch, State } from '@stencil/core';

@Component({
  tag: 'text-area',
  styleUrl: 'text-area.css',
  shadow: true,
})
export class TextArea {
  @Prop() placeholder: string = 'Enter text...';
  @Prop() value: string = '';

  @Event() valueChange!: EventEmitter<string>;

  @State() internalValue: string = this.value;

  componentWillLoad() {
    this.internalValue = this.value;
  }

  @Watch('value')
  valueChanged(newValue: string) {
    this.internalValue = newValue;
  }

  handleInput = (event: Event) => {
    const input = event.target as HTMLTextAreaElement;
    this.internalValue = input.value;
    this.valueChange.emit(input.value);
  };

  render() {
  return (
    <>
      <ix-style-loader></ix-style-loader>

      <textarea
        onInput={this.handleInput}
        ref={(el) => {
          if (el) {
            el.placeholder = this.placeholder;
            el.value = this.internalValue;
          }
        }}
      ></textarea>
    </>

  );
}

  }
